export default new (class {
  /* ===================================== Type ===================================== */

  /**
   * @public
   * @method
   * @name isFunction
   * @description 
   * @param {*} value 
   * @returns {boolean}
   */
  isFunction(value) {
    return Object.prototype.toString.call(value) === '[object Function]'
  }
  /* ===================================== DOM ===================================== */

  /**
   * @public
   * @method
   * @name $
   * @description  HTML 
   * @param {string} selectors  DOM 
   * @returns {Element | null}  null Element
   */
  $(selectors) {
    return document.querySelector(selectors)
  }

  /**
   * @public
   * @method
   * @name _h
   * @description  tagName  HTML 
   * @param {string} tagName 
   * @param {object} propMap  class 
   * @param {string} text 
   * @returns {Element | HTMLUnknownElement}  tagName  HTMLUnknownElement
   */
  h(tagName, propMap = {}, text) {
    const ele = document.createElement(tagName)
    for (let prop in propMap) {
      ele.setAttribute(prop, propMap[prop])
    }
    if (text) {
      ele.appendChild(document.createTextNode(text))
    }
    return ele
  }

  /* ===================================== EVENT ===================================== */

  /**
   * @public
   * @method
   * @name bindEvents
   * @param {string} events 
   * @param {function} handler 
   * @param {Element | undefined} element 
   * @returns {void}
   */
  bindEvents(events, handler, element = $('body')) {
    events.split(' ').forEach(event => {
      element.addEventListener(event, handler, false)
    })
  }

  /**
   * @public
   * @method
   * @name unbindEvents
   * @param {string} events 
   * @param {function} handler 
   * @param {Element | undefined} element 
   * @returns {void}
   */
  unbindEvents(events, handler, element = $('body')) {
    events.split(' ').forEach(event => {
      element.removeEventListener(event, handler, false)
    })
  }

  /* ===================================== Perf ===================================== */

  /**
   * @public
   * @method
   * @name throttle
   * @description 
   * @param {function} method 
   * @param {object} context 
   * @param {number} delay 
   * @returns {function} 
   */
  throttle(method, context = {}, delay = 4, ...outParams) {
    return function(...innerParams) {
      clearTimeout(context.$$tId)
      context.$$tId = setTimeout(function() {
        method.apply(context, [...outParams, ...innerParams])
      }, delay)
    }
  }
})()
